.stopwatch {
  grid-row-gap: 23px;
  width: 100%;
  background-color: #eaeaea;
  justify-items: center;
  padding-top: 25px;
  display: grid;
}

.circle {
  height: 270px;
  width: 270px;
  background-color: #72e59a;
  border: 2px solid;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.time {
  font-size: 48px;
  font-weight: 300;
}

.controls {
  justify-content: space-between;
  display: flex;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

#playButton {
  width: 137px;
  height: 60px;
  display: block;
}

#pauseButton {
  width: 150px;
  height: 60px;
  display: none;
}

#resetButton {
  width: 150px;
  height: 60px;
}

#playButton, #pauseButton, #resetButton {
  border-radius: 15px;
}

